import Link from "next/link";

interface FoodItem {
  id: string;
  name: string;
  description: string;
  price: number;
  image: string;
}

interface FoodItemCardProps {
  food: FoodItem;
}

export default function FoodItemCard({ food }: FoodItemCardProps) {
  return (
    <div className="border rounded-md p-4 flex flex-col space-y-3 shadow-sm hover:shadow-md transition">
      <div className="w-full h-48 bg-gray-200 rounded overflow-hidden">
        <img
          src={food.image}
          alt={food.name}
          className="w-full h-full object-cover"
          onError={(e) => {
            (e.target as HTMLImageElement).src = "/fallback-image.png";
          }}
        />
      </div>
      <h3 className="text-xl font-semibold">{food.name}</h3>
      <p className="text-sm text-muted-foreground">{food.description}</p>
      <p className="font-bold">${food.price.toFixed(2)}</p>
      <Link
        href={`/food/${food.id}`}
        className="mt-auto px-4 py-2 bg-secondary text-secondary-foreground rounded text-center"
      >
        View Details
      </Link>
    </div>
  );
}
